                    <span class="mdui-chip-title">{{nodeName}}</span></div></div>
                <div class="mdui-typo-body-2 item">‰∏äÊ¨°Ê£ÄÊµãÔºö{{timestamp}}</div>
            </div>
            <div class="mdui-card-actions">
                <button @click="logout" class="mdui-btn mdui-ripple">ÈÄÄÂá∫ÁôªÂΩï</button>
                <button @click="del" class="mdui-btn mdui-ripple mdui-color-red">Âà†Èô§Ë¥¶Âè∑</button>
            </div>
        </div>

        <div class="mdui-dialog" id="delete">
        <div class="mdui-dialog-title">Âà†Èô§</div>
        <div class="mdui-dialog-content">Á°ÆËÆ§‰ªéÁ≥ªÁªü‰∏≠ÁßªÈô§‰Ω†ÁöÑcookieÂêóÔºü</div>
