<script>
import axios from 'axios'
import mdui from 'mdui'
export default {
    name:"My",
    data:function(){
        return {
            baseUrl: "",
            nick: "",
            status: 1,
            nodeName: "",
            timestamp:"",
            cid: ""
        }
    },
    methods:{
        getInfo(){
            this.baseUrl= localStorage.getItem("baseUrl") == "" ? "node1" : localStorage.getItem("baseUrl");
            console.log("baseurl ::" + this.baseUrl)
            var self= this
            this.cid= localStorage.getItem("cid")
            this.nodeName=localStorage.getItem("nodeName")
            //Ëé∑ÂèñË¥¶Âè∑‰ø°ÊÅØ
            axios.post(this.baseUrl+"/checkcookie",{cid:localStorage.getItem("cid"),token:localStorage.getItem("token")}).then(function(res){
                self.nick= res.data.data.remarks
                self.status= res.data.data.status
                self.timestamp=new Date(res.data.data.timestamp).toLocaleString()
            }).catch(function(res){
                mdui.snackbar("ÊúçÂä°Âô®ËøûÊé•Â§±Ë¥•ÔºÅ")
                console.log(res)
            })
        },
        logout(){
            localStorage.removeItem("cid")
            localStorage.removeItem("nodeName")
            localStorage.removeItem("baseUrl")
            localStorage.removeItem("token")
            this.$router.replace("/")
        },
        del(){
            var instD = new mdui.Dialog('#delete');
            instD.open()
        },
        checkDel(){
            var self= this
            axios.post(this.baseUrl + "/delete",{
                cid : this.cid,
            }).then(function(res){
                mdui.snackbar(res.data.data)
                self.logout()
            })
        }

    },
    mounted:function(){
        this.getInfo()
    },
    computed:{
        nowStatusStr(){
            if(this.status==0){
                return "Ê≠£Â∏∏"
            }else{
                return "Â∑≤Â§±Êïà"
            }
        },
        nowStatusClass(){
            if(this.status==0){
                return ["mdui-chip-icon", "mdui-color-blue"]
            }else{
                return ["mdui-chip-icon", "mdui-color-red"]
            } 
        }
    }
}
</script>

